import { AfterContentInit, AfterViewChecked, Component, ContentChildren, ElementRef, EventEmitter, Input, isDevMode, OnInit, Output, QueryList, TemplateRef, ViewChild, OnDestroy } from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { FormBuilder } from '@angular/forms';
import { fromEvent, Observable } from 'rxjs';
import { debounceTime, map, startWith, takeUntil } from 'rxjs/operators';
import { format, parseISO } from 'date-fns';

import { MyaTableService } from '../services/table.service';

import { BaseDirective } from '@mya-controls/core/directives';
import { ColumnCustomHeaderDirective } from '@mya-controls/table/directives/table-column-custom-header.directive';
import { ColumnCustomDirective } from '@mya-controls/table/directives/table-column-custom.directive';

import { TableColumns } from '@mya-controls/table/types/mya-table-columns';
import { ColumnsType } from '@mya-controls/table/types/columns-type';
import { TableDefault } from '@mya-controls/table/types/tableDefault';

@Component({
  selector: 'mya-table',
  template: (require('./table.component.html').default || require('./table.component.html')).length ? (require('./table.component.html').default || require('./table.component.html')) : '',
  styles: [(require('./table.component.scss').default || require('./table.component.scss')).length ? (require('./table.component.scss').default || require('./table.component.scss')) : '']
})

export class TableComponent extends BaseDirective implements OnInit, AfterContentInit, AfterViewChecked, OnDestroy {

  constructor(private formBuilder: FormBuilder, private elementRef: ElementRef, private myAdminTableService: MyaTableService) {
    super();
  }

  ngAfterContentInit(): void {
    if ((!this._customColumnsArray || this._customColumnsArray.length === 0) && this.customColumns && this.customColumns.length > 0) {
      this.cacheRowValues();
    }

    this._customColumnHeadersArray = this.customColumnHeaders?.toArray() ?? [];
    this.setCustomHeaderBindings();
  }

  ngAfterViewChecked(): void {
    this.calculatePageSize();
  }

  ngOnInit(): void {
    this._data = [];
    this._selection.clear();
    if (this.dataChange) {
      this.dataChange.pipe(takeUntil(this.ngUnsubscribe)).subscribe((row) => {
        if (row) {
          this.cacheRowValues(row);
        } else {
          this.cacheRowValues();
        }
      });
    } else {
      if (this.editFormTemplate && isDevMode()) {
        console.warn('rowChanged must be defined if you are using an edit form!');
      }
    }

    this.subscribeToCloseEditForm();
    const checkScreenSize = () => document.body.offsetWidth < 1024;
    const screenSizeChanged$ = fromEvent(window, 'resize').pipe(debounceTime(500), map(checkScreenSize), takeUntil(this.ngUnsubscribe));
    this._isScreenSmall$ = screenSizeChanged$.pipe(startWith(checkScreenSize()), takeUntil(this.ngUnsubscribe));
    const screenSizeChangedPaging$ = fromEvent(window, 'resize').pipe(debounceTime(1000), takeUntil(this.ngUnsubscribe));
    screenSizeChangedPaging$.subscribe(() => {
      if (this._isTableView) {
        this.myAdminTableService.calculationTableSizeSet = false;
        this.calculatePageSize();
      }
    });
  }

  ngOnDestroy(): void {
    this.myAdminTableService.calculationTableSizeSet = false;
  }

  //Input Properties
  /* Configured column values for table*/
  @Input() get p_columnsToDisplay(): string[] {
    return this.columns.filter((c: TableColumns) => c.isHidden !== true).map((c: TableColumns) => c.binding);
  }
  _columnsToDisplayListView = ['ListColumn'];

  @Input() get columnsToDisplay(): any[] {
    return this.p_columnsToDisplay;
  }
  set columnsToDisplay(val: any) {
    if (val && val.length > 0) {
      this.columns = val.filter((c: TableColumns) => c.isHidden !== true);
      this._listViewItems = this.columns.filter((x: TableColumns) => x.isListView === true);
      if (this._listViewItems.length > 5 && isDevMode()) {
        this.showErrorView();
        this._showListViewError = true;
      }
      const listViewTitle = this.columns.filter((x: TableColumns) => x.isTitleProperty === true);
      if (listViewTitle.length > 1 && isDevMode()) {
        this.showErrorView();
        this._showListTitleError = true;
      }
    }
  }

  @Input() public get columns(): TableColumns[] {
    return this._columns.filter((c: TableColumns) => c.isHidden !== true);
  }
  set columns(val: TableColumns[]) {
    if (val) {
      this._columns = val;
    }
  }
  _columns!: TableColumns[];
  _listViewItems!: TableColumns[];

  /* Configured page size for table row items*/
  @Input() get pageSize(): number {
    return this._pageSize;
  }
  set pageSize(val: number) {
    if (!this._isAutoPageSize) {
      this._pageSize = val;
      this.setPaginator(val);
    }
  }
  protected _pageSize = 10;

  /* Configured page index for table row items*/
  @Input() get pageIndex(): number {
    return this._pageIndex;
  }
  set pageindex(val: number) {
    this._pageIndex = val;
    this._dataSource.paginator = this._paginator;
    if (val && this._dataSource.paginator?.pageIndex) {
      this._dataSource.paginator.pageIndex = val;
    }
  }
  protected _pageIndex = 0;

  /* Configured autoPageSize for table row items*/
  @Input() get autoPageSize(): boolean {
    return this._isAutoPageSize;
  }
  set autoPageSize(val: boolean) {
    if (val) {
      this._isAutoPageSize = val;
      this.myAdminTableService.setAutoTableSize(val);
    }
  }
  _isAutoPageSize = false;

  //TODO this should be removed
  /* Configured hide page size for table*/
  @Input() get hidePageSize(): boolean {
    return this._hidePageSize;
  }
  set hidePageSize(val: boolean) {
    if (val) {
      this._hidePageSize = val;
    }
  }
  protected _hidePageSize = true;

  /* Table data values*/
  @Input() get datasource(): any {
    return this._dataSource.data;
  }
  set datasource(val: any) {
    if (val) {
      this._dataSource.data = val;
      this.cacheRowValues();
      this._selection.clear();
    }
  }
  _data: any = [];
  _dataSource = new MatTableDataSource<any>(this._data);
  _tempData = new MatTableDataSource<any>();

  /* Text displayed when no data loaded in Table*/
  @Input() get emptyTableText(): string {
    return this._emptyTableText;
  }
  set emptyTableText(val: string) {
    if (val) {
      this._emptyTableText = val;
    }
  }
  _emptyTableText: string = 'No Data Loaded';

  //TODO this should be removed
  /* Text filtered datasource for data in Table */
  @Input() get filteredDataSource() {
    return this._dataSource.filteredData;
  }
  set filteredDataSource(val: any) {
    if (val) {
      // @ts-ignore
      val.forEach((row) =>
        this._dataSource.filteredData.map((item) => {
          if (item.id === row.id) {
            this.selectRow(item);
          }
        })
      );
    }
  }

  //TODO this should be removed
  /* setting this to true will enable the ability to show a form in mobile view*/
  @Input() get mobileForm(): boolean {
    return this._showEditFormMobile;
  }
  set mobileForm(val: boolean) {
    if (val) {
      this._showEditFormMobile = val;
    }
  }
  _showEditFormMobile = false;

  /* Text displayed when editing item in list view*/
  @Input() get pageTitle(): any {
    return this._listViewPageTitle;
  }
  set pageTitle(val: string) {
    if (val) {
      this._listViewPageTitle = val;
    }
  }
  _listViewPageTitle: string = '';

  /* hide list view overrides double click */
  @Input() get disableListView(): boolean {
    return this._disableListView;
  }
  set disableListView(val: boolean) {
    if (val) {
      this._disableListView = val;
    }
  }
  protected _disableListView = false;

  //TODO event emitter
  @Input() get buttonClickListView(): boolean {
    //TODO ASK JZ
    return this._disableListView;
  }
  set buttonClickListView(val: any) {
    if (val) {
      this._buttonClick = val;
      this.showListView();
    }
  }

  /* Allow multiple rows to be selected, default is false */
  @Input() get multiSelect() {
    return this._isMultiSelect;
  }
  set multiSelect(val: boolean) {
    this._selection.clear();
    if (val) {
      this._originalMultiSelectState = val;
      this._isMultiSelect = val;
    }
  }
  _isMultiSelect = false;

  /* select all rows in table */
  @Input() get selectAll(): boolean {
    return this._isSelectAll;
  }
  set selectAll(val: boolean) {
    if (val) {
      this._isSelectAll = val;
      this.selectAllRows();
    }
  }
  protected _isSelectAll = true;

  /* Define custom table name for table default is a random GUID, this is used to avoid 2 table implementation on page with same columns. */
  @Input() get tableName(): string {
    if (this._localTableName === undefined) {
      return this._localTableName;
    } else {
      return this._localTableName;
    }
  }
  set tableName(val: string) {
    if (val) {
      this._localTableName = val;
    }
  }
  _localTableName: string = this.generateUUID();

  @Input() get tableDefaultValues(): TableDefault {
    return this._tableDefaultValues;
  }
  set tableDefaultValues(values: TableDefault) {
    this._tableDefaultValues = values;
  }
  _tableDefaultValues: TableDefault = new TableDefault();

  /* Open selected item from table rows */
  @Input() get openSelectedValue() {
    return this._selectedValue;
  }
  set openSelectedValue(val: any) {
    if (val) {
      this._selectedValue = val;
      this._selection.toggle(this._selectedValue);
      this.showListView();
    }
  }
  protected _selectedValue = null;

  /* Used to notify data cache of date changes */
  // @ts-ignore
  @Input() public dataChange: Observable<any>;

  /* Edit form template for editing row item */
  // @ts-ignore
  @Input() public editFormTemplate: TemplateRef<any>;
  /* Action bar template for table */
  // @ts-ignore
  @Input() public actionToolbarTemplate: TemplateRef<any>;
  /* Filter control form template for table data */
  // @ts-ignore
  @Input() public filterTemplate: TemplateRef<any>;
  /* Button Collection template for table */
  // @ts-ignore
  @Input() public buttonColTemplate: TemplateRef<any>;
  /* Checkbox Collection template for table */
  // @ts-ignore
  @Input() public checkBoxColTemplate: TemplateRef<any>;


  /*  */
  @Input() hasSelectAllCheckbox = false;

  /*  */
  //TODO this should be removed, ask why it was added
  @Input() noSortingIcon = false;

  /*  */
  //TODO this should be removed, ask why it was added
  @Input() circleIcon = false;

  /*  */
  @Input() isSecondaryTable = false;

  /*  */
  //TODO this should be removed, ask why it was added
  @Input() noSelection = false;

  /* Used to close form on custom action */
  @Input() public closeListView: EventEmitter<any> = new EventEmitter<any>();

  //TODO rename to close dataLoading and change to event
  /* Used to show loading indication */
  @Input() dataLoading: boolean = false;

  @Input() public sortListView = false;

  /* Emitted when a selection has changed from table row */
  @Output() selectionChange: EventEmitter<any> = new EventEmitter<any>();
  /* Emitted when a filter value has changed table row */
  @Output() updateFilterPredicateChange: EventEmitter<MatTableDataSource<any>> = new EventEmitter<MatTableDataSource<any>>();
  /* Emitted when a multi selection has changed on table rows */
  @Output() multiSelectionChange: EventEmitter<any[]> = new EventEmitter<any[]>();
  /* Emitted when a page index has changed */
  @Output() pageChange: EventEmitter<any> = new EventEmitter<any>();


  @ViewChild(MatPaginator) set matPaginator(mp: MatPaginator) {
    this._paginator = mp;
    this._dataSource.paginator = this._paginator;
    this.setDataSourceAttributes();
  }

  @ViewChild(MatSort) set matSort(ms: MatSort) {
    this._sort = ms;
    this._dataSource.sort = this.noSortingIcon ? null : ms;
    this.setDataSourceAttributes();
  }
  @ViewChild('paginator', { static: true })

  @ContentChildren(ColumnCustomDirective) customColumns!: QueryList<ColumnCustomDirective>;
  @ContentChildren(ColumnCustomHeaderDirective) customColumnHeaders!: QueryList<ColumnCustomHeaderDirective>;

  _isTableView = true;
  _isListView = false;
  _isErrorView = false;
  _showListViewError = false;
  _showListTitleError = false;

  _buttonClick: any; // value of the button click event passed in by parent component
  _selection = new SelectionModel<any>(true, []);
  _lastSelectedRow: [] | undefined; // this is to save the last selected row, so going from table to list view keeps the selected value.
  _isScreenSmall$!: Observable<boolean>;
  _originalMultiSelectState = false; //this is the original value passed in by the parent component, this should never change.

  _customColumnsArray!: ColumnCustomDirective[];
  _customColumnHeadersArray!: ColumnCustomHeaderDirective[];

  _paginator!: MatPaginator;

  private _sort!: MatSort;

  clearSelected(): void {
    this._dataSource.data = [];
    this._selection.clear();
  }

  stringToPath(str: string): string[] {
    return str.split('.');
  }

  subscribeToCloseEditForm(): void {
    if (this.closeListView) {
      this.closeListView.pipe(takeUntil(this.ngUnsubscribe)).subscribe(() => {
        this.cancel();
      });
    }
  }

  getVal(columnName: string, row: any): any {
    if (!row) {
      return undefined;
    }
    if (!columnName) {
      return '';
    }
    const properties = Array.isArray(columnName) ? columnName : columnName.split('.');
    // @ts-ignore
    return properties.reduce((prev, curr) => prev && prev[curr], row);
  }

  //TODO ASK JZ, Remove Not used
  // isTitleColumn(column: string): boolean {
  //   return this.columns?.find((c) => c.binding === column)?.isTitleProperty;
  // }

  selectedRowChange(_selection: any): void {
    if (!this.noSelection) {
      this.selectRow(_selection);
      this.selectionChange.emit(_selection);
    }
  }

  selectRow(row: any): void {
    if (!this.noSelection) {
      if (row && !this.multiSelect) {
        if (this._selection.selected.length === 1 && this._selection.selected[0] !== row) {
          this._selection.toggle(this._selection.selected[0]);
        }
        this._lastSelectedRow = row;
        this._selection.toggle(row);
      } else if (row && this.multiSelect) {
        this._lastSelectedRow = row;
        this._selection.toggle(row);
      }

      this.multiSelectionChange.emit(this._selection.selected);
    }
  }

  selectAllRows(): void {
    this.checkSelected() ? this._selection.clear() : this._dataSource.filteredData.forEach((row) => this._selection.select(row));
    this.multiSelectionChange.emit(this._selection.selected);
  }

  checkSelected(): boolean {
    const numFiltered = this._dataSource.filteredData.length;
    const numSelected = this._selection.selected.length;
    return numSelected === numFiltered;
  }

  setDataSourceAttributes(): void {
    this._dataSource.paginator = this._paginator;
    this._dataSource.sort = this.noSortingIcon ? null : this._sort;
    this._dataSource.sortingDataAccessor = (item, property) => {
      return property.split('.').reduce((o, p) => o && o[p], item);
    };
    this.updateFilterPredicateChange.emit(this._dataSource);
  }

  changeTableLayout(row: any): void {
    this.selectionChange.emit(row);
    this.showListView();
  }

  showErrorView(): void {
    if (isDevMode()) {
      this._isErrorView = true;
      this._isListView = false;
      this._isTableView = false;
    }
  }

  showListView(): void {
    if (!this._disableListView) {
      this._isErrorView = false;
      this._isListView = true;
      this._isTableView = false;
      if (this._originalMultiSelectState) {
        this._selection.clear();
        this._selection.toggle(this._lastSelectedRow);
        this._isMultiSelect = false;
      } else {
        this._selection.toggle(this._lastSelectedRow);
      }
      this._tempData.data = this._dataSource.data;
      const selected = this._selection.selected[0];
      if (selected) {
        this._dataSource.data = [selected, ...this._dataSource.data.filter((item) => item !== selected)];
      }
    }
  }

  showTableView(): void {
    this._dataSource.data = this._tempData.data;
    if (this._originalMultiSelectState) {
      this._isErrorView = false;
      this._isMultiSelect = true;
      this._isListView = false;
      this._isTableView = true;
    } else {
      this._isErrorView = false;
      this._isListView = false;
      this._isTableView = true;
    }
  }

  cancel(): void {
    this.showTableView();
    this._selection.clear();
    this._selection.deselect();
    this.selectionChange.emit(null);
  }

  selectAndView(row: any): void {
    this._lastSelectedRow = row;
    this.selectionChange.emit(row);
    this.showListView();
  }

  private cacheRowValues(row: any = null): void {
    this._customColumnsArray = this.customColumns?.toArray() ?? [];
    if (row) {
      this.createCacheBindings(row);
    } else {
      for (const dataRow of this._dataSource.data) {
        this.createCacheBindings(dataRow);
      }
    }
  }

  private createCacheBindings(row: any): void {
    const columnBindings: any = {};
    this.setColumnBindings(columnBindings, row);
    this.setCustomTitleBindings(columnBindings);

    row[this.tableName] = columnBindings;
  }

  onPage(event: any): void {
    this.pageChange.emit(event);
  }

  private setColumnBindings(columnBindings: any, row: any) {
    if (!this.columns) {
      return;
    }

    for (const column of this.columns) {
      this.setColumnBinding(columnBindings, row, column);
    }
  }

  private setColumnBinding(columnBindings: any, row: any, column: any) {
    columnBindings[column.binding] = this.getVal(column.binding, row);

    if (column.type == ColumnsType.Date) {
      const date = parseISO(this.getVal(column.binding, row));
      if (!isNaN(<any>date)) {
        columnBindings[column.binding] = format(date, column.dateFormat ?? 'MM/dd/yyyy HH:mm:ss');
      }
    }

    if (column.bindingListView) {
      columnBindings[column.bindingListView] = this.getVal(column.bindingListView, row);
    }

    if (this._customColumnsArray) {
      const customIndex = this._customColumnsArray.findIndex((c) => c.column === column.binding);
      if (customIndex > -1) {
        columnBindings[`${column.binding}-custom`] = customIndex;
      }
    }
  }

  private setCustomTitleBindings(columnBindings: any) {
    if (this._customColumnsArray) {
      const titlePropertyIndex = this._customColumnsArray.findIndex((c) => c.column === 'titleProperty');
      if (titlePropertyIndex > -1) {
        columnBindings['titleProperty'] = titlePropertyIndex;
      }
    }
  }

  private setCustomHeaderBindings() {
    if (this._customColumnHeadersArray) {
      for (const column of this.columns) {
        const customHeaderIndex = this._customColumnHeadersArray.findIndex((c) => c.column === column.binding);
        if (customHeaderIndex > -1) {
          column.headerIndex = customHeaderIndex;
        }
      }
    }
  }

  //This is a Beta Feature that needs some more calculation logic when tables are not loaded without Toolbars and center aligned.
  private calculatePageSize() {
    if (this._isAutoPageSize && !this.myAdminTableService.calculationTableSizeSet && this.elementRef.nativeElement.firstElementChild !== undefined && this.elementRef.nativeElement.firstElementChild !== null && this.elementRef.nativeElement.firstElementChild.scrollHeight > 0) {
      this.myAdminTableService.calculationTableSizeSet = true;
      const contentTableHeight = this.elementRef.nativeElement.firstElementChild.scrollHeight;
      const allowed = Math.round((contentTableHeight / 64) * 0.87);
      this.pageSize = Math.floor(allowed - (allowed % 2));
      this.setPaginator(this._pageSize);
    }
  }

  private setPaginator(val: number) {
    if (this._paginator) {
      this._dataSource.paginator = this._paginator;
      if (val && this._dataSource.paginator?.pageSize) {
        this._dataSource.paginator.pageSize = val;
      }
    } else if (val && this._dataSource.paginator?.pageSize) {
      this._dataSource.paginator.pageSize = val;
    }
  }

  private generateUUID() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      // tslint:disable-next-line:no-bitwise
      const r = (Math.random() * 16) | 0,
        v = c == 'x' ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
  }

}
