import { Injectable } from '@angular/core';
import { Subject} from "rxjs";

@Injectable()
export class MyaTableService {

  calculationTableSizeSet: boolean = false;

  private isAutoTableSize = new Subject<boolean>();
  isAutoTableSize$ = this.isAutoTableSize.asObservable();

  public setAutoTableSize(value:boolean){
    this.isAutoTableSize.next(value);
  }
}
