import { PipeTransform, Pipe } from '@angular/core';

@Pipe({
  name: 'stringDecode'
})

export class StringDecodePipe implements PipeTransform {
  transform(value: string, useLiteral: boolean = false): string {
    return this.DecodeHtml(value, useLiteral);
  }

  DecodeHtml(html: string, useLiteral: boolean = false): string {
    if (html === null || html === undefined) {
      return '';
    }
    const txt = document.createElement('textarea');
    if (useLiteral) {
      txt.innerHTML = '<pre class="defaultPre">' + html + '</pre>';
    } else {
      txt.innerHTML = html;
    }
    return txt.value;
  }
}
